body {
  margin: 0;
  padding: 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.34;
  font-family: "PT Mono", "Courier New", monospace; }

*,
*::before,
*::after {
  box-sizing: border-box; }

img {
  max-width: 100%;
  height: auto; }

a {
  text-decoration: none; }

input,
textarea,
select,
button {
  font: inherit; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

@font-face {
  font-family: "PT Mono";
  src: url("../fonts/PTMono-Regular.woff") format("woff"), url("../fonts/PTMono-Regular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/RobotoCondensed-Regular.woff") format("woff"), url("../fonts/RobotoCondensed-Regular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/RobotoCondensed-Bold.woff") format("woff"), url("../fonts/RobotoCondensed-Bold.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

.container {
  max-width: 1440px;
  min-width: 1024px;
  margin: 0 auto; }
  @media (max-width: 1023px) {
    .container {
      max-width: 1023px;
      min-width: 768px; } }
  @media (max-width: 767px) {
    .container {
      max-width: 767px;
      min-width: 320px; } }

.header {
  height: 881px;
  background-color: #1b2d37;
  background: url("../img/promo-bg.png") no-repeat right center, url("../img/background.jpg") repeat-x; }

.header__wrapper {
  padding-top: 28px;
  padding-left: 10px;
  padding-right: 10px; }

.main-nav {
  display: flex;
  padding-left: 2px;
  padding-right: 6px; }

.main-nav__logo {
  margin-right: auto;
  margin-top: 5px; }

.main-nav__list {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  min-width: 660px; }

.main-nav__item {
  display: flex;
  align-items: center;
  margin-right: 88px; }
  .main-nav__item a {
    color: #ffffff; }
  .main-nav__item:nth-child(2n) {
    margin-right: 50px; }
  .main-nav__item:last-child {
    margin: 0; }
    .main-nav__item:last-child a {
      font-family: "Roboto Condensed", "Helvetica", sans-serif;
      font-size: 14px;
      letter-spacing: 0.05em;
      text-transform: uppercase;
      border-radius: 5px;
      border: 1px solid #ffffff;
      padding: 12px 27px 13px 26px; }

.main-nav__icon {
  margin-right: 20px;
  margin-top: -1px; }
  .main-nav__icon--phone {
    margin-right: 16px; }

.promo {
  padding-top: 170px; }
  .promo h1 {
    font-family: "Roboto Condensed", "Helvetica", sans-serif;
    font-size: 80px;
    letter-spacing: 0.07em;
    text-transform: uppercase;
    line-height: 94px;
    color: #ffffff;
    margin-bottom: 27px; }
  .promo a {
    display: inline-block;
    color: #ffffff;
    font-size: 14px;
    line-height: 16px; }

.promo > a {
  transform: rotate(-90deg) translate(-128px, -35px);
  opacity: 0.5; }

.promo__info {
  padding-left: 96px;
  max-width: 720px; }
  .promo__info p {
    color: #ffffff;
    font-size: 22px;
    position: relative;
    line-height: 34.5px;
    margin: 0;
    margin-bottom: 45px; }
    .promo__info p::before {
      content: "";
      width: 66px;
      height: 2px;
      background-color: #ffffff;
      position: absolute;
      top: 17px;
      left: -93px; }
  .promo__info a {
    font-family: "Roboto Condensed", "Helvetica", sans-serif;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    border-radius: 5px;
    font-size: 16px;
    background-color: #00b2ff;
    padding: 18px 45px 20px 45px; }
